.header__navegacion {
  padding: 2rem 8rem 2rem 6rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: #424242;
}
@media (min-width: 768px) {
  .header__navegacion {
    flex-direction: row;
  }
}
.header__nav {
  margin-bottom: 2rem;
  margin-top: 2rem;
  font-size: 2.3rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  font-weight: bold;
  text-transform: uppercase;
}
.header__nav a {
  color: #FFFFFF;
  transition-property: color;
  transition-duration: 0.3s;
}
.header__nav a:hover {
  color: #035aac;
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__nav {
    font-size: 1.7rem;
    margin-bottom: 0;
    gap: 3rem;
    flex-direction: row;
    margin-top: 0;
    text-transform: none;
  }
}
.header__degradado {
  margin-top: 6rem;
  color: transparent;
  text-transform: uppercase;
  font-weight: 900;
  font-size: 4rem;
  cursor: pointer;
  background: linear-gradient(to right, #cddc39 -55%, #0481f7 100%);
  background-clip: text;
}
@media (min-width: 768px) {
  .header__degradado {
    color: transparent;
    font-size: 6rem;
    margin-top: 0;
    background: linear-gradient(to right, #cddc39 -20%, #0481f7 100%);
    background-clip: text;
  }
}
@media (min-width: 1024px) {
  .header__degradado {
    color: transparent;
    font-size: 6rem;
    margin-top: 0;
    background: linear-gradient(to right, #cddc39 -60%, #0481f7 100%);
    background-clip: text;
  }
}
.header__contenido-icono-login {
  position: relative;
  left: 2.2rem;
  top: 2rem;
}
@media (min-width: 768px) {
  .header__contenido-icono-login {
    left: 2.2rem;
    top: 0;
  }
}
@media (min-width: 1024px) {
  .header__contenido-icono-login {
    left: 2.2rem;
    top: 0;
  }
}
.header__contenido-icono-registro {
  position: relative;
  left: 2.6rem;
  bottom: 0.5rem;
  top: 2rem;
}
@media (min-width: 768px) {
  .header__contenido-icono-registro {
    left: 2.6rem;
    bottom: 0.5rem;
    top: -0.5rem;
  }
}
@media (min-width: 1024px) {
  .header__contenido-icono-registro {
    left: 2.6rem;
    bottom: 5rem;
  }
}
.header__icono-login {
  display: none;
  filter: invert(100%);
}
@media (min-width: 768px) {
  .header__icono-login {
    display: block;
    max-width: 1.9rem;
    min-width: 1.9rem;
  }
}
@media (min-width: 1024px) {
  .header__icono-login {
    display: block;
    max-width: 1.9rem;
    min-width: 1.9rem;
  }
}
.header__icono-registro {
  display: none;
  filter: invert(100%);
}
