.hero {
  background-image: url("/img/hero4.jpg");
  position: relative;
  height: 80rem;
  width: 100%;
  background-repeat: no-repeat;
  background-position: center center;
}
.hero::before {
  content: "";
  background-color: #000;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.6;
}
.hero__contenido {
  position: relative;
  color: #FFFFFF;
}
.hero__contenido h2 {
  font-size: 5.2rem;
